<template>
  <div class="jdy-content fleft">
    <div class="jdy-content-inner">
      <div class="jdy-tab">
        <el-form :model="ruleSearchForm" :inline="true" ref="ruleSearchForm" class="ml10">
          <el-row class="search-row pt10">
            <el-button type="default" class="ml10 btnbg fright" @click="goback" >返回</el-button>
            <el-button type="default" class="ml10 btnbg fright" @click="save" :disabled="btnFlag">保存</el-button>
          </el-row>
        </el-form>
      </div>
      <div class="role_info mt30">
        <el-form :model="tickForm" :rules="rules" ref="tickForm" class="demo-form-inline" label-width="150px">
          <el-row>
              <!-- <el-col :span="12" class="p10">
                  <el-form-item label="角色编号:" prop="rRoleNo">
                    <el-input v-model="tickForm.rRoleNo"></el-input>
                  </el-form-item>
              </el-col> -->
              <!-- <el-col :span="8">
                  <el-form-item label="排序:" prop="sort">
                      <el-input v-model="tickForm.sort"></el-input>
                  </el-form-item>
              </el-col> -->
              <el-col :span="24" class="p10">
                  <el-form-item label="角色名称:" prop="rRoleName">
                    <el-input v-model="tickForm.rRoleName"></el-input>
                  </el-form-item>
              </el-col>
          </el-row>
          <el-row>
              <el-col :span="24" class="p10">
                  <el-form-item label="备注说明:">
                    <el-input v-model="tickForm.rRemark" type="textarea"></el-input>
                  </el-form-item>
              </el-col>
          </el-row>
        </el-form>
      </div>
      <div class="sysRight">
        <div class="sysTitle">
            <!-- <el-checkbox v-model="allRjght">系统权限</el-checkbox> -->
            <!-- <el-button type="default" class="ml10 btnbg fright mt5" @click="close">收起</el-button> -->
            <el-button type="default" class="ml10 btnbg fright mt5" @click="spear">展开</el-button>
            <el-button type="default" class="ml10 btnbg fright mt5" @click="checkCancel">取消</el-button>  
            <el-button type="default" class="ml10 btnbg fright mt5" @click="setCheckedKeys">全选</el-button>           
        </div>
        <div class="sysMain">
          <el-tree :data="treeData" @check-change="handleCheckChange" ref='tree' show-checkbox node-key="id" :default-expanded-keys="expandData" :default-checked-keys="checkData" :props="defaultProps">
          </el-tree>          
        </div>
      </div>
        <!-- 分页   begin-->
        <!-- <div class="clearfix">
          <el-pagination class="fright mt20" @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="pageSizeAll" layout="prev, pager, next, jumper" :total="tableDataTotal">
          </el-pagination>
        </div> -->
        <!-- 分页   end-->
    </div>
  </div>
  <!--jdy-content end-->
</template>

<script>
  import API from './../api/index';
  import api_prefix from '@/assets/js/apiUrl';

  export default {
    name: 'roleManager',
    data() {
      return {
        currentPage:1,
        pageSizeAll:20,
        tableDataTotal:0,
        ruleSearchForm:{},
        tickForm:{
          rCompanyId:null,
          id:null,
          rRoleNo:null,
          sort:null,
          rRoleName:null,
          rRemark:null,
          checkedKeys:[]
        },
        expandData:[], //展开数据项
        checkData:[], //选中数据项
        resData:{
          expand:[],
          check:[],
        },
        allRjght:false,
        searchValue:'',
        tableData:[],
        publicId:'',
        accesstoken:'',
        time:'',
        // 树状结构
        treeData:[],
        defaultProps: {
          children: 'children',
          label: 'label'
        },
        /*校验*/
        rules: {
            rRoleName: [{type: 'string', required: true, message: '请输入角色名称', trigger: 'change' },
            {  max: 15, message: '长度在15个字符以内', trigger: 'blur' }],
        },
        pIds:[],
        btnFlag:false,       
      }
    },
    created(){

    },
    mounted() {
      this.publicId = JSON.parse(sessionStorage.loginData).appId;
      // this.getTableData();
      if(this.$route.query.rRoleName!=null){
        this.tickForm={
          rRoleNo:this.$route.query.rRoleNo,
          rRoleName:this.$route.query.rRoleName,
          rRemark:this.$route.query.rRemark,
          id:this.$route.query.rId,
        };
      };
      this.tickForm.rCompanyId=this.$route.query.rCompanyId 
      this.getRole();
    },
    methods: {
      goback(){
        this.$router.push({name:'roleManager'})
      },
      handleCheckChange(data, checked, indeterminate){
        console.log(this.pIds)
      },      
      save(){ 
        this.btnFlag=true;   
        this.pIds.removeArr(0);
        this.tickForm.checkedKeys=this.$refs.tree.getCheckedKeys();
        let newCheckedKeys = JSON.parse(JSON.stringify(this.tickForm.checkedKeys));
        let keys = newCheckedKeys;
        function DeepFind(tree){
          for(let i = 0; i < tree.length; i++){
            console.log('i:',i);
            if( tree[i].children.length > 0 ){
              DeepFind(tree[i].children);
              // return false;
            }
            for(let j = 0; j < keys.length; j++){
              if( keys[j] == tree[i].id ){
                // for( let k = 0; k < keys.length; k++){
                //   if( keys[k] == tree[i].pId ){
                //     return false;
                //   }
                // }
                // console.log('pid:',tree[i].pId);
                newCheckedKeys.push(tree[i].pId);
              }
            }
          }
        }
        // console.log(this.treeData);
        
        DeepFind(this.treeData);
        // return false;
        this.tickForm.checkedKeys = newCheckedKeys;
        this.tickForm.checkedKeys.removeArr(0);
        // return false;
        this.$refs["tickForm"].validate((valid) => {
        if(valid){          
            let params=JSON.parse(JSON.stringify(this.tickForm));
            let limit=/^[A-Za-z0-9]+$/;
            if(!limit.test(params.rRoleNo)){
                this.$message.error('角色编号只能为数字或字母！');
                return;
            }                       
            API.roleUpdate(params).then(response => {
              if(response.body.code==0){
                this.$alert("保存成功","温馨提示",{type: 'success'});
                this.$router.push({ name: 'roleManager'})
              }else{
                this.$alert(response.body.message,"温馨提示",{type: 'error'});
              };
              this.btnFlag=false;
            })
        }else{
            this.$message.error('提交失败了');
            this.btnFlag=false;
        }
        });        
      },
      spear(){
        this.expandData=this.resData.expand
      },
      close(){
        this.expandData=[];
      },
      checkCancel(){
        this.$refs.tree.setCheckedKeys([]);
      },
      setCheckedKeys(){
        this.expandData=this.resData.expand
        this.checkData=this.resData.expand
        this.$refs.tree.setCheckedKeys(this.checkData);
      },
      getRole(){
        let params={
          id:this.$route.query.rId,
          rCompanyId:this.$route.query.rCompanyId
        };
        API.roleEdit(params).then(response => {
          if(response.body.code==0){
            let resData=response.body.body
            this.treeData=resData.tree;
            this.resData.expand=resData.allTreeId;             
            this.resData.check=resData.checkedKeys;
            this.$refs.tree.setCheckedKeys(resData.checkedKeys);
          }else{

          };
        })
      },
      handleCurrentChange(val){
        this.currentPage = val;
        this.getTableData();
      },
      handleRowChange(){

      },
    }

  }


</script>
<style scoped lang='less'>
.el-form-item{
    margin-bottom: 0;
}
.yjk-orderList .el-table th>.cell,.yjk-orderList .el-table td>.cell{
    text-align: center;
}
.role_info{
    height: auto;
    padding-bottom: 30px;
    border: 1px solid #dfe6ec;
    .el-row{
      padding:15px 20px 0 10px;
    }
}
.sysRight{
    margin-top: 20px;
    height: auto;
    padding-bottom: 30px;
    border: 1px solid #dfe6ec;  
    .sysTitle{
      background: #e5e9f2;
      height:50px;
      line-height: 50px;
      padding: 0 10px;
    }
    .sysMain{
      margin: 10px;
      border: 1px solid #dfe6ec;
    }
}

</style>






